import {css, SerializedStyles} from '@emotion/core';
import styled from '@emotion/styled';

const justifyMap = {
  end: 'flex-end',
  'space-around': 'space-around',
  'space-between': 'space-between',
  start: 'flex-start',
};

const alignMap = {
  ...justifyMap,
  stretch: 'stretch',
};

export function box(props): SerializedStyles {
  return css`
    display: flex;
    flex-direction: ${props.column ? 'column' : 'row'};
    justify-content: ${justifyMap[props.justify] || 'center'};
    align-items: ${alignMap[props.align] || 'center'};
    flex-wrap: ${props.wrap ? 'wrap' : ' no-wrap'};
    flex-grow: ${props.grow ? 1 : 0};
  `;
}

const Box = styled.div`
  ${box};
`;

export const Row = styled(Box)`
  flex-direction: row;
`;

export const Column = styled(Box)`
  flex-direction: column;
`;

export default Box;
