import useMeasure from '@kevinastone/use-measure';
import React from 'react';
import {animated, useTransition} from 'react-spring';

function AccordianItem({height, children, ...props}) {
  const [bind, {height: viewHeight}] = useMeasure<HTMLDivElement>();

  return (
    <animated.div
      style={{
        height: height.interpolate({output: [0, viewHeight]}),
      }}
      {...props}
    >
      <animated.div {...bind}>{children}</animated.div>
    </animated.div>
  );
}

export default function useAccordian(items, keyFunc, wrapperProps) {
  const transitions = useTransition(items, keyFunc, {
    enter: {height: 1},
    from: {height: 0},
    leave: {height: 0},
    trail: 50,
  });

  return render =>
    transitions.map(({key, item, props}, index) => (
      <AccordianItem key={key} {...wrapperProps} {...props}>
        {render(item, index)}
      </AccordianItem>
    ));
}
