import {ActionsUnion, createAction} from '@martin_hotell/rex-tils';
import {asResult, isSuccess} from '@kevinastone/result';

import {navigate} from 'gatsby';
import idx from 'idx.macro';

export const SET_QUERY = 'SET_QUERY';
export const SET_RESULTS = 'SET_RESULTS';
export const NEXT_SELECTION = 'NEXT_SELECTION';
export const PREVIOUS_SELECTION = 'PREVIOUS_SELECTION';
export const RESET = 'RESET';

export const Actions = {
  setQuery: query => createAction(SET_QUERY, query),
  setResults: results => createAction(SET_RESULTS, results),
  nextSelection: () => createAction(NEXT_SELECTION),
  previousSelection: () => createAction(PREVIOUS_SELECTION),
  reset: () => createAction(RESET),
};

export type Actions = ActionsUnion<typeof Actions>;

export function activateSelection(selection, results): void {
  const url = idx(results, _ => _[selection.current].url);
  if (url) {
    navigate(url);
  }
}

export async function setQuery(dispatch, api, query): Promise<void> {
  dispatch(Actions.setQuery(query));

  const results = await asResult(api(query));
  if (isSuccess(results)) {
    dispatch(Actions.setResults(results));
  }
}
