import React from 'react';
import {css} from '@emotion/core';
import styled from '@kevinastone/theme-blog/styled';
import useAccordian from '@kevinastone/accordian';
import {Column} from '@kevinastone/flex-box';
import {rhythm} from '@kevinastone/theme-blog';
import Result from './result';

const List = styled(Column)`
  border-radius: ${rhythm(1 / 8)};
  list-style-type: none;
  margin: ${rhythm(1 / 8)} 0 0 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 100%;
  ${props => props.theme.shadow}
`;

export default function({results, selection}) {
  const accordian = useAccordian(results, item => item.ref, {
    css: css`
      width: 100%;
    `,
  });

  return (
    <List>
      {accordian((item, index) => (
        <Result selected={index === selection} result={item} />
      ))}
    </List>
  );
}
